import axios from "axios";
import { takeLatest, put, all, call } from "redux-saga/effects";
import bazresiTypes from "./Types";
import {
  failureFetchBazresi,
  successFetchBazresi,
  successSaveBazresi,
  failureSaveBazresi,
} from "./actions";

const getdata = () => {
  return axios.get("/cars/viewset/firstbazdid/").then((res) => res.data);
};

const savedata = (data) => {
  return axios
    .post("/cars/viewset/firstbazdid/", data)
    .then((res) => res.data)
    .catch((error) => console.log(error));
};
export function* getListCars() {
  try {
    const data = yield call(getdata);
    if (data) {
      yield put(successFetchBazresi(data));
    } else {
      yield put(failureFetchBazresi("بازیابی دیتا با مشکل مواجه شد"));
    }
  } catch (error) {
    put(failureFetchBazresi(error));
  }
}

export function* savedatabazresi({
  payload: {
    car,
    shisheh,
    chragh_jelo,
    chragh_aghab,
    chragh_rahnama,
    arm,
    lastik,
    camarband,
    tormoz,
    tormoz_dasti,
    bogh,
    barf,
    ayneh_rast,
    ayneh_chap,
    bogh_aghab,
    sensor,
    khamoshkon,
    box_komak,
    parcham,
    chragh_gardon,
    zanjir,
  },
}) {
  try {
    const data = {
      car,
      shisheh,
      chragh_jelo,
      chragh_aghab,
      chragh_rahnama,
      arm,
      lastik,
      camarband,
      tormoz,
      tormoz_dasti,
      bogh,
      barf,
      ayneh_rast,
      ayneh_chap,
      bogh_aghab,
      sensor,
      khamoshkon,
      box_komak,
      parcham,
      chragh_gardon,
      zanjir,
    };

    const bazresi = yield call(savedata, data);

    if (bazresi) {
      yield put(successSaveBazresi("ذخیره شد"));
    } else {
      yield put(failureSaveBazresi("در روند ذخیره سازی مشکل ایجاد شد"));
    }
  } catch (error) {
    put(failureSaveBazresi("در روند ذخیره سازی مشکل ایجاد شد"));
  }
}

export function* getBazresi() {
  yield takeLatest(bazresiTypes.STATRTFETCH, getListCars);
}
export function* saveBazresi() {
  yield takeLatest(bazresiTypes.STARTSAVE, savedatabazresi);
}
export function* bazresiSaga() {
  yield all([call(getBazresi), call(saveBazresi)]);
}
