import React from "react";
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);
const useStyles = makeStyles({
  root: {
    width: "100%",
  },
  container: {
    maxHeight: 440,
  },
  table: {
    minWidth: 700,
  },
});

export default function ListTable({ cars }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              <StyledTableCell>شمارنده</StyledTableCell>
              <StyledTableCell align="right">نام راننده</StyledTableCell>
              <StyledTableCell align="right">نام خانوادگی راننده</StyledTableCell>
              <StyledTableCell align="right">موبایل</StyledTableCell>
              <StyledTableCell align="right">نوع خودرو</StyledTableCell>
              <StyledTableCell align="right">شماره خودرو </StyledTableCell>
              <StyledTableCell align="right">پلاک خودرو </StyledTableCell>
              <StyledTableCell align="right">مدل خودرو </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {cars
              ? cars.map((row) => (
                  <StyledTableRow  key={row.id} className={classes.root}>
                    <StyledTableCell component="th" scope="row">
                      {row.id}
                    </StyledTableCell>
                    <StyledTableCell align="right">{row.Name}</StyledTableCell>
                    <StyledTableCell align="right">{row.Famil}</StyledTableCell>
                    <StyledTableCell align="right">{row.mobile}</StyledTableCell>
                    <StyledTableCell align="right">{row.Type_Car}</StyledTableCell>
                    <StyledTableCell align="right">{row.Number_Car}</StyledTableCell>
                    <StyledTableCell align="right">{row.Plak_Car}</StyledTableCell>
                    <StyledTableCell align="right">{row.Model_Car}</StyledTableCell>
                  </StyledTableRow>
                ))
              : "ماشینی وجود ندارد"}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  );
}
