import React, { Component } from "react";
import { connect } from "react-redux";
import { saveStart } from "../../redux/car/actions";
import {
  FormControl,
  OutlinedInput,
  InputLabel,
  Button, Typography
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import SaveIcon from "@material-ui/icons/Save";

const styles = (theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
});

class CarAccount extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Name: "",
      Famil: "",
      Mobile: "",
      Type_Car: "",
      Number_Car: "",
      Plak_Car: "",
      Model_Car: 90,
    };
  }

  handleChange = (event) => {
    const { value, name } = event.target;

    this.setState({ [name]: value });
  };

  formclick = (event) => {
    event.preventDefault();
    const { saveStart } = this.props;
    const {
      Name,
      Famil,
      Mobile,
      Type_Car,
      Number_Car,
      Plak_Car,
      Model_Car,
    } = this.state;
    saveStart({
      Name,
      Famil,
      Mobile,
      Type_Car,
      Number_Car,
      Plak_Car,
      Model_Car,
    });
  };
  render() {
    const { classes,message } = this.props;
    const {
      Name,
      Famil,
      Mobile,
      Type_Car,
      Number_Car,
      Plak_Car,
      Model_Car,
    } = this.state;
    
    return (
      <div>
      {
        message?<Typography variant='h4'>{message}</Typography>:''
      }
      

        <form
          className={classes.root}
          autoComplete="off"
          onSubmit={this.formclick}
        >
          <FormControl variant="outlined">
            <InputLabel htmlFor="Name">نام راننده</InputLabel>
            <OutlinedInput
              name="Name"
              value={Name}
              onChange={this.handleChange}
              id="Name"
              label="نام راننده"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Famil">نام خانوادگی راننده</InputLabel>
            <OutlinedInput
              name="Famil"
              value={Famil}
              onChange={this.handleChange}
              id="Famil"
              label="نام خانوادگی راننده"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Mobile">موبایل</InputLabel>
            <OutlinedInput
              name="Mobile"
              value={Mobile}
              onChange={this.handleChange}
              id="Mobile"
              label="موبایل"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Type_Car">نوع خودرو</InputLabel>
            <OutlinedInput
              name="Type_Car"
              value={Type_Car}
              onChange={this.handleChange}
              id="Type_Car"
              label="نوع خودرو"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Number_Car">شماره خودرو</InputLabel>
            <OutlinedInput
              name="Number_Car"
              value={Number_Car}
              onChange={this.handleChange}
              id="Number_Car"
              label="شماره خودرو"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Plak_Car">شماره پلاک</InputLabel>
            <OutlinedInput
              name="Plak_Car"
              value={Plak_Car}
              onChange={this.handleChange}
              id="Plak_Car"
              label="شماره پلاک"
            />
          </FormControl>
          <FormControl variant="outlined">
            <InputLabel htmlFor="Model_Car">مدل خودرو</InputLabel>
            <OutlinedInput
              name="Model_Car"
              value={Model_Car}
              onChange={this.handleChange}
              id="Model_Car"
              label="مدل خودرو"
            />
          </FormControl>

          <FormControl>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              size="large"
              className={classes.button}
              endIcon={<SaveIcon style={{ paddingRight: "10px" }} />}
            >
              ذخیره
            </Button>
          </FormControl>
        </form>
      </div>
    );
  }
}
const mapStateToProps = (state) => {

  return {
    message: state.car.message,
  };
};
const mapDispatchToProps = (dispatch) => ({
  saveStart: (carData) => dispatch(saveStart(carData)),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles, { withTheme: true })(CarAccount));
