import React from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./components/header/Header.component";
import HomePage from "./pages/home/HompePage";
import CarPage from "./pages/car/CarPage";
import ListCarsPage from "./pages/car/ListCarsPage";
import FirstBazresiPage from './pages/firstBazresi/FirstBazresiPage'
import ListFirstBazresiPage from './pages/firstBazresi/ListFirstBazresiPage'
import MonthBazresiPage from './pages/monthBazresi/MonthBazresiPage'
import FindMonthBazresiPage from './pages/monthBazresi/FindMonthBazresiPage'
import AnbarPage from './pages/anbar/AnbarPage'
import { Grid } from "@material-ui/core";



const useStyles = makeStyles((theme) => ({
  main: {
    marginTop:'10px',
    padding:'5px'
  },
  
}));

function App() {
  const classes = useStyles();
  return (
    <div className="App">
      <Header />
      
      <Grid container className={classes.main}>
        <Grid item xs={1} />
        
        <Grid item xs={10} >
          <Switch>
            <Route exact path="/" component={HomePage} />
            <Route path="/car" component={CarPage} />
            <Route path="/listcars" component={ListCarsPage} />
            <Route path="/firstbazresi" component={FirstBazresiPage} />
            <Route path="/listfirstbazresi" component={ListFirstBazresiPage} />
            <Route path="/monthbazresi" component={MonthBazresiPage} />
            <Route path="/findmonthbazresi" component={FindMonthBazresiPage} />
            <Route path="/anbar" component={AnbarPage} />
          </Switch>
        </Grid>
        <Grid item xs={1} />
      </Grid>
    </div>
  );
}

export default App;
