import React, { Component } from "react";
import { connect } from "react-redux";
import { withStyles } from "@material-ui/core/styles";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import SaveIcon from "@material-ui/icons/Save";
import {
  FormControl,
  OutlinedInput,
  InputLabel,
  Button,
  Typography,
  Grid,
} from "@material-ui/core";
import { startSaveBazresi } from "../../redux/firstBazresi/actions";
const styles = (theme) => ({
  root: {},
});
export class FirstBazresi extends Component {
  constructor(props) {
    super(props);
    this.state = {
      car: "",
      shisheh: "NA",
      chragh_jelo: "NA",
      chragh_aghab: "NA",
      chragh_rahnama: "NA",
      arm: "NA",
      lastik: "NA",
      camarband: "NA",
      tormoz: "NA",
      tormoz_dasti: "NA",
      bogh: "NA",
      barf: "NA",
      ayneh_rast: "NA",
      ayneh_chap: "NA",
      bogh_aghab: "NA",
      sensor: "NA",
      khamoshkon: "NA",
      box_komak: "NA",
      parcham: "NA",
      chragh_gardon: "NA",
      zanjir: "NA",
    };
  }
  handleChange = (event) => {
    if (event.target.checked) {
      this.setState({ [event.target.name]: "SA" });
    } else {
      this.setState({ [event.target.name]: "NA" });
    }
  };
  handleChangecar = (event) => {
    const { value, name } = event.target;

    this.setState({ [name]: value });
  };

  formsubmit = (event) => {
    event.preventDefault();
    const { startSaveBazresi } = this.props;
    const {
      car,
      shisheh,
      chragh_jelo,
      chragh_aghab,
      chragh_rahnama,
      arm,
      lastik,
      camarband,
      tormoz,
      tormoz_dasti,
      bogh,
      barf,
      ayneh_rast,
      ayneh_chap,
      bogh_aghab,
      sensor,
      khamoshkon,
      box_komak,
      parcham,
      chragh_gardon,
      zanjir,
    } = this.state;
    startSaveBazresi({
      car,
      shisheh,
      chragh_jelo,
      chragh_aghab,
      chragh_rahnama,
      arm,
      lastik,
      camarband,
      tormoz,
      tormoz_dasti,
      bogh,
      barf,
      ayneh_rast,
      ayneh_chap,
      bogh_aghab,
      sensor,
      khamoshkon,
      box_komak,
      parcham,
      chragh_gardon,
      zanjir,
    });
  };
  render() {
    const { classes, message } = this.props;
    return (
      <div>
        {this.props.message ? (
          <Typography variant="h4">{message}</Typography>
        ) : (
          ""
        )}
        <form onSubmit={this.formsubmit} autoComplete="off">
          <FormControl variant="outlined" style={{  marginBottom:"20px" }}>
            <InputLabel htmlFor="car">شماره ماشین</InputLabel>
            <OutlinedInput
              name="car"
              value={this.state.car}
              onChange={this.handleChangecar}
              id="car"
              label="شماره ماشین"
            />
          </FormControl>

          <FormGroup row>
            <Grid container spacing={2}>
              <Grid
                container
                xs={12}
                justify="space-between"
                alignItems="center"
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.shisheh === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="shisheh"
                    />
                  }
                  label="وضعیت شیشه"
                />{" "}
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.chragh_jelo === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="chragh_jelo"
                    />
                  }
                  label="چراغ های جلو"
                />{" "}
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.chragh_aghab === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="chragh_aghab"
                    />
                  }
                  label="چراغ های عقب"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={
                        this.state.chragh_rahnama === "SA" ? true : false
                      }
                      onChange={this.handleChange}
                      name="chragh_rahnama"
                    />
                  }
                  label="چراغ های راهنما"
                />{" "}
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.arm === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="arm"
                    />
                  }
                  label="ارم شرکت"
                />
              </Grid>
              <Grid
                container
                xs={12}
                justify="space-between"
                alignItems="center"
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.lastik === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="lastik"
                    />
                  }
                  label="وضعیت لاستیک"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.camarband === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="camarband"
                    />
                  }
                  label="کمربند ایمنی"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.tormoz === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="tormoz"
                    />
                  }
                  label="وضعیت ترمز"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.tormoz_dasti === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="tormoz_dasti"
                    />
                  }
                  label="وضعیت ترمز دستی"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.bogh === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="bogh"
                    />
                  }
                  label="بوق"
                />
              </Grid>
              <Grid
                container
                xs={12}
                justify="space-between"
                alignItems="center"
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.barf === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="barf"
                    />
                  }
                  label="برف پاکن و شیشه پاک کن"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.ayneh_rast === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="ayneh_rast"
                    />
                  }
                  label="اینه راست"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.ayneh_chap === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="ayneh_chap"
                    />
                  }
                  label="اینه چپ"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.bogh_aghab === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="bogh_aghab"
                    />
                  }
                  label="بوق دنده عقب"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.sensor === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="sensor"
                    />
                  }
                  label="سنسور"
                />
              </Grid>

              <Grid
                container
                xs={12}
                justify="space-between"
                alignItems="center"
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.khamoshkon === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="khamoshkon"
                    />
                  }
                  label="خاموش کن دستی"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.box_komak === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="box_komak"
                    />
                  }
                  label="جعبه کمک های اولیه"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.parcham === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="parcham"
                    />
                  }
                  label="پرچم"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.chragh_gardon === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="chragh_gardon"
                    />
                  }
                  label="چراغ گردون"
                />
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={this.state.zanjir === "SA" ? true : false}
                      onChange={this.handleChange}
                      name="zanjir"
                    />
                  }
                  label="زنجیر چرخ"
                />
              </Grid>
            </Grid>
          </FormGroup>
          <FormControl style={{ marginRight:"50%", marginTop:"20px" }}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              size="large"
              className={classes.button}
              endIcon={<SaveIcon style={{ paddingRight: "10px" }} />}
            >
              ذخیره
            </Button>
          </FormControl>
        </form>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    message: state.bazresi.message,
  };
};
const mapDispatchToProps = (dispatch) => ({
  startSaveBazresi: (bazresidata) => dispatch(startSaveBazresi(bazresidata)),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles, { withTheme: true })(FirstBazresi));
