import bazresiTypes from "./Types";

export const startFetchBazresi = () => ({
  type: bazresiTypes.STATRTFETCH,
});

export const successFetchBazresi = (data) => ({
  type: bazresiTypes.SUCCESSFETCH,
  payload: data,
});

export const failureFetchBazresi = (error) => ({
  type: bazresiTypes.FAILUREFETCH,
  payload: error,
});

export const startSaveBazresi = (data) => ({
  type: bazresiTypes.STARTSAVE,
  payload: data,
});

export const successSaveBazresi = (message) => ({
  type: bazresiTypes.SUCCESSSAVE,
  payload: message,
});

export const failureSaveBazresi = (error) => ({
  type: bazresiTypes.FAILURESAVE,
  payload: error,
});
