import React from "react";
import { withRouter } from "react-router-dom";
import {
  AppBar,
  Toolbar,
  Box,
  Typography,
  BottomNavigation,
  BottomNavigationAction,
} from "@material-ui/core";
import {
  Home,
  DriveEta,
  Commute,
  AccountBalance,
  AirlineSeatLegroomNormal,
  Assignment,
} from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  banner: {
    background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
    border: 0,
    borderRadius: 3,
    boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
    color: "white",
    height: 150,
    padding: "0 30px",
  },
  root: {
    width: 800,
  },
  appbar: {
    alignItems: 'center',
  }
}));

const Header = (props) => {
  const { history } = props;
  const classes = useStyles();

  return (
    <>
      <Box className={classes.banner}>
        <Typography variant="h2" align="center" color="primary">
          نرم افزار PM شرکت خدماتی اطلس کویر
        </Typography>
        <Typography variant="h4" align="center" color="primary">
          (نسخه آزمایش)
        </Typography>
      </Box>
      <AppBar position="static" className={classes.appbar}>
        <Toolbar>
          <BottomNavigation
            className={classes.root}
            showLabels
            onChange={(event, newValue) => {
              history.push(newValue);
            }}
          >
            <BottomNavigationAction value="/" label="خانه" icon={<Home />} />
            <BottomNavigationAction
              value="/car"
              label="ثبت ماشین"
              icon={<DriveEta />}
            />
            <BottomNavigationAction
              value="/listcars"
              label="لیست ماشین ها"
              icon={<Commute />}
            />
            <BottomNavigationAction
              value="/firstbazresi"
              label="بازدید اولیه"
              icon={<Assignment />}
            />
            <BottomNavigationAction
              value="/listfirstbazresi"
              label="لیست بازدید اولیه"
              icon={<Assignment />}
            />
            <BottomNavigationAction
              value="/monthbazresi"
              label="بازدید ماهیانه"
              icon={<AirlineSeatLegroomNormal />}
            />
            <BottomNavigationAction
              value="/findmonthbazresi"
              label="مشاهده بازدید ماهیانه"
              icon={<AirlineSeatLegroomNormal />}
            />
            <BottomNavigationAction
              value="/anbar"
              label="انبار"
              icon={<AccountBalance />}
            />
          </BottomNavigation>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default withRouter(Header);
