import carTypes from "./Types";
import axios from "axios";
import { takeLatest, put, all, call } from "redux-saga/effects";
import {
  saveSuccess,
  saveFailure,
  successFetchCars,
  failurFetchCars,
} from "./actions";

const savedata = (data) => {
  return axios.post("/cars/viewset/car/", data).then((res) => res.data);
};

const getdata = () => {
  return axios.get("/cars/viewset/car/").then((res) => res.data);
};

export function* getCars() {
  try {
    const cars = yield call(getdata);
    if (cars) {
      yield put(successFetchCars(cars));
    } else {
      yield put(failurFetchCars("بازیابی با مشکل روبه رو شد"));
    }
  } catch (error) {
    put(failurFetchCars(error));
  }
}
export function* saveCar({
  payload: { Name, Famil, Mobile, Type_Car, Number_Car, Plak_Car, Model_Car },
}) {
  try {
    const data = {
      Name: Name,
      Famil: Famil,
      mobile: Mobile,
      Type_Car: Type_Car,
      Number_Car: Number_Car,
      Plak_Car: Plak_Car,
      Model_Car: Model_Car,
    };
    const car = yield call(savedata, data);

    if (car) {
      const { Name, id } = car;
      const a = "راننده خودرو با نام ";
      const b = "و شناسه ";
      const c = "ذخیره گردید";
      yield put(saveSuccess(`${a} ${Name} ${b} ${id} ${c}`));
    } else {
      yield put(saveFailure("خطا در هنگام ذخیره سازی"));
    }
  } catch (error) {
    put(saveFailure(error));
  }
}

export function* saveStart() {
  yield takeLatest(carTypes.STARTSAVE, saveCar);
}
export function* getCarsStart() {
  yield takeLatest(carTypes.STARTFETCHCAR, getCars);
}
export function* carSaga() {
  yield all([call(saveStart), call(getCarsStart)]);
}
