import React, { Component } from "react";
import { connect } from "react-redux";
import { startFetchCars } from "../../redux/car/actions";
import ListTable from "./ListTable";

class ListCars extends Component {
  componentDidMount() {
    const { startFetchCars } = this.props;
    startFetchCars();
  }
  render() {
    return (
      <div>
        <ListTable cars={this.props.cars} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    cars: state.car.cars,
  };
};
const mapDispatchToProps = (dispatch) => ({
  startFetchCars: () => dispatch(startFetchCars()),
});
export default connect(mapStateToProps, mapDispatchToProps)(ListCars);
