import bazresiTypes from "./Types";

const INITIAL_STATE = {
  Listcars: null,
  isFetching: false,
  successFull: false,
  message: undefined,
};

const bazresiReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case bazresiTypes.SUCCESSFETCH:
      return {
        ...state,
        Listcars: action.payload,
        message: undefined,
      };

    case bazresiTypes.FAILUREFETCH:
      return {
        ...state,
        Listcars: null,
        message: action.payload,
      };
    case bazresiTypes.SUCCESSSAVE:
      return {
        ...state,
        message: action.payload,
      };
    case bazresiTypes.FAILURESAVE:
      return {
        ...state,
        message: action.payload,
      };
    default:
      return state;
  }
};

export default bazresiReducer;
