import carTypes from "./Types";

export const saveStart = (carData) => ({
  type: carTypes.STARTSAVE,
  payload: carData,
});

export const saveSuccess = (successMessage) => ({
  type: carTypes.SAVESUCCESS,
  payload: successMessage,
});

export const saveFailure = (errorMessage) => ({
  type: carTypes.SAVEFAILURE,
  payload: errorMessage,
});

export const startFetchCars = () => ({
  type: carTypes.STARTFETCHCAR,
});

export const successFetchCars = (cars) => ({
  type: carTypes.SUCCESSFETCHCAR,
  payload: cars,
});

export const failurFetchCars = (error) => ({
  type: carTypes.FAILUREFETCHCAR,
  payload: error,
});
