import React from "react";
import cars from "../../assets/cars.jpg";
import Grid from "@material-ui/core/Grid";
const HompePage = () => {
  return (
    <div>
      <Grid container>
        <Grid item xs={6}>
          <img src={cars} alt="atlas mess " height="300px" />
        </Grid>
        <h2>شرکت خدماتی اطلس مس کویر</h2>
        <Grid item xs={6}></Grid>
      </Grid>
    </div>
  );
};

export default HompePage;
