import carTypes from "./Types";

const INITIAL_STATE = {
  cars: null,
  isFetching: false,
  successFull: false,
  message: undefined,
};

const CarReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case carTypes.SAVESUCCESS:
      return {
        ...state,
        successFull: true,
        message: action.payload,
      };
    case carTypes.SAVEFAILURE:
      return {
        ...state,
        successFull: false,
        message: action.payload,
      };
    case carTypes.SUCCESSFETCHCAR:
        return{
            ...state,
            cars:action.payload,
            isFetching:false
        }
    case carTypes.FAILUREFETCHCAR:
        return{
                ...state,
                cars:null,
                isFetching:false,
                message:action.payload
            }
    default:
      return state;
  }
};

export default CarReducer;
