import React, { Component } from "react";
import { connect } from "react-redux";

import { startFetchBazresi } from "../../redux/firstBazresi/actions";
import { withStyles } from "@material-ui/core/styles";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@material-ui/core";
const styles = (theme) => ({
  root: {},
  table: {
    minWidth: 650,
  },
});

class ListFirstBazresi extends Component {
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    const { startFetchBazresi } = this.props;
    startFetchBazresi();
  }

  changedic(value) {
    switch (value) {
      case "SA":
        return "سالم";
      case "MA":
        return "معیوب";
      case "DA":
        return "دارد";
      case "NA":
        return "ندارد";
      default:
        return value;
    }
  }
  render() {
    const { classes, Listcars } = this.props;
    return (
      <div>
        <TableContainer component={Paper}>
          <Table className={classes.table} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell align="right">ماشین</TableCell>
                <TableCell align="right">وضعیت شیشه</TableCell>
                <TableCell align="right">چراغ های جلو</TableCell>
                <TableCell align="right">چراغ های عقب</TableCell>
                <TableCell align="right">چراغ های راهنما</TableCell>
                <TableCell align="right">آرم شرکت</TableCell>
                <TableCell align="right">وضعیت لاستیک</TableCell>
                <TableCell align="right">کمربند ایمنی</TableCell>
                <TableCell align="right">سیستم ترمز</TableCell>
                <TableCell align="right">ترمز دستی</TableCell>
                <TableCell align="right">بوق</TableCell>
                <TableCell align="right">پرف پاک کن</TableCell>
                <TableCell align="right">اینه سمت راست</TableCell>
                <TableCell align="right">اینه سمت چپ</TableCell>
                <TableCell align="right">بوق دنده عقب</TableCell>
                <TableCell align="right">سنسور</TableCell>
                <TableCell align="right">خاموش کننده دستی</TableCell>
                <TableCell align="right">جعبه کمکها</TableCell>
                <TableCell align="right">پرچم</TableCell>
                <TableCell align="right">چراغ گردون</TableCell>
                <TableCell align="right">زنجیر</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {Listcars
                ? Listcars.map((row) => (
                    <TableRow key={row.car}>
                      <TableCell component="th" scope="row">
                        {row.car}
                      </TableCell>

                      <TableCell align="right">
                        {this.changedic(row.shisheh)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.chragh_jelo)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.chragh_aghab)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.chragh_rahnama)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.arm)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.lastik)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.camarband)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.tormoz)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.tormoz_dasti)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.bogh)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.barf)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.ayneh_rast)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.ayneh_chap)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.bogh_aghab)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.sensor)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.khamoshkon)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.box_komak)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.parcham)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.chragh_gardon)}
                      </TableCell>
                      <TableCell align="right">
                        {this.changedic(row.zanjir)}
                      </TableCell>
                    </TableRow>
                  ))
                : "ماشینی وجود ندارد"}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    Listcars: state.bazresi.Listcars,
  };
};
const mapDispatchToProps = (dispatch) => ({
  startFetchBazresi: () => dispatch(startFetchBazresi()),
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles, { withTheme: true })(ListFirstBazresi));
